/* global Office */

Office.onReady(() => {});

function prefillTo(event) {
  const item = Office.context.mailbox.item;

  item.notificationMessages.replaceAsync("cmd", {
    type: Office.MailboxEnums.ItemNotificationMessageType.InformationalMessage,
    message: "COMMAND FIRED"
  });

  item.to.setAsync(
    [{ displayName: "Helpdesk", emailAddress: "helpdesk@contoso.com" }],
    () => event.completed()
  );
}

Office.actions.associate("prefillTo", prefillTo);
